.form {
  width: 302px;
}
@media screen and (max-width: 375px) {
  form {
    width: 90%;
  }
}
.input {
  display: block;
  width: 100%;
  box-sizing: border-box;
  background-color: var(--input-backround-color);
  border: 1px solid var(--high-priority-bg-color);
  border-radius: 8px;
  padding: 14px 18px;
  box-shadow: 0px 4px 16px rgba(17, 17, 17, 0.1);
  color: var(--primary-text-color);
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: -2%;
  margin-bottom: 24px;
}
.input:focus,
.input:hover {
  color: var(--primary-text-color);
  border-color: var(--primary-text-color);
}
.label {
  position: relative;
}

.error {
  position: absolute;
  bottom: -21px;
  right: 0;
  font-size: 13px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: -1%;
  opacity: 0.9;
  color: #e66666;
}
